import { Component, Input, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { QueriesService } from 'src/app/_services/queries.service';
import { phoneValidator, trimValidator } from 'src/app/_utils/formValidationUtils';

@Component({
  selector: 'app-contacts',
  templateUrl: './contacts.component.html',
  styleUrls: ['./contacts.component.scss']
})
export class ContactsComponent implements OnInit {

  @Input('contacts1') contacts1: any
  @Input('contacts2') contacts2: any

  form = new FormGroup({
    name: new FormControl('', [Validators.required, Validators.maxLength(128), Validators.minLength(8), trimValidator]),
    email: new FormControl('', [Validators.required, Validators.maxLength(128), trimValidator, Validators.email]),
    // phone: new FormControl('', [Validators.required, Validators.maxLength(16), Validators.minLength(8), phoneValidator]),
    subject: new FormControl('', [Validators.required, Validators.maxLength(128), Validators.minLength(8), trimValidator]),
    message: new FormControl('', [Validators.required, Validators.maxLength(512), Validators.minLength(8), trimValidator]),
});

  constructor(
    private _query: QueriesService
  ) { }

  ngOnInit(): void {
  }

  saveData() {
    this.form.markAllAsTouched();
    this.form.updateValueAndValidity();


    if (this.form.valid) {
        const data = {
            name: this.form.controls.name.value,
            // phone: this.form.controls.phone.value,
            email: this.form.controls.email.value,
            subject: this.form.controls.subject.value,
            message: this.form.controls.message.value
        }
        console.log('**************************', data);
        this._query.postRequest(data).subscribe(data => {
          this.form.reset()
        })
        
     
    }
}

}
